      messages: [],
      inputText: '',
      isSending: false,
      isTyping: false,
      scrollIntoView: '',
      msgIdPrefix: 'msg-',
      sessionId: 'default', // ÂΩìÂâç‰ºöËØùID
      currentSessionName: 'ÈªòËÆ§‰ºöËØù', // ÂΩìÂâç‰ºöËØùÂêçÁß∞
