      emojiList: [
        'üòä', 'üòÉ', 'üòÑ', 'üòÅ', 'üòÜ', 'üòÖ', 'ü§£', 'üòÇ',
        'üôÇ', 'üôÉ', 'üòâ', 'üòä', 'üòá', 'ü•∞', 'üòç', 'ü§©',
        'üòò', 'üòó', '‚ò∫Ô∏è', 'üòö', 'üòô', 'ü•≤', 'üòã', 'üòõ',
        'üòú', 'ü§™', 'üòù', 'ü§ë', 'ü§ó', 'ü§≠', 'ü§´', 'ü§î',
        'ü§ê', 'ü§®', 'üòê', 'üòë', 'üò∂', 'üòè', 'üòí', 'üôÑ',
        'üò¨', 'ü§•', 'üòå', 'üòî', 'üò™', 'ü§§', 'üò¥', 'üò∑',
        'ü§í', 'ü§ï', 'ü§¢', 'ü§Æ', 'ü§ß', 'ü•µ', 'ü•∂', 'üò∂‚Äçüå´Ô∏è',
        'üòµ', 'ü§Ø', 'ü§†', 'ü•≥', 'ü•¥', 'üòé', 'ü§ì', 'üßê',
        'üëç', 'üëé', 'üëè', 'üôè', 'üí™', '‚ù§Ô∏è', 'üíî', 'üíØ'
      ]
    }
  },
  
  async onLoad() {
    console.log('[CHAT] AIÂØπËØùÈ°µÈù¢Âä†ËΩΩ');
    
    // ÂàùÂßãÂåñÂ≠òÂÇ®
    await chatStorage.init();
    
    // Âä†ËΩΩÁî®Êà∑ÁöÑAI‰∫∫Ê†ºÂÅèÂ•Ω
    this.currentPersonality = getPersonalityPreference();
    console.log('[CHAT] ÂΩìÂâçAI‰∫∫Ê†º:', this.currentPersonality);
    
    // Âä†ËΩΩ‰ºöËØùÂàóË°®
    await this.loadSessions();
    
    // Âä†ËΩΩÂéÜÂè≤Ê∂àÊÅØ
    await this.loadHistoryMessages();
    
    // Âä†ËΩΩÊî∂ËóèÂàóË°®
    this.loadFavorites();
    
    // Â¶ÇÊûúÊ≤°ÊúâÂéÜÂè≤Ê∂àÊÅØÔºåÊ∑ªÂä†Ê¨¢ËøéÊ∂àÊÅØ
    if (this.messages.length === 0) {
      const personalityConfig = getPersonalityConfig(this.currentPersonality);
      this.addAIMessage(`ÊÇ®Â•ΩÔºÅÊàëÊòØÊÇ®ÁöÑÂøÉÁêÜÊîØÊåÅAIÔºà${personalityConfig.name}Ôºâ„ÄÇÊó†ËÆ∫ÊÇ®ÈÅáÂà∞‰ªÄ‰πàÂõ∞Êâ∞ÔºåÈÉΩÂèØ‰ª•ÂíåÊàëÂÄæËØâ„ÄÇÊàë‰ºöËÆ§ÁúüÂÄæÂê¨ÔºåÂπ∂Â∞ΩÊàëÊâÄËÉΩÁªô‰∫àÊîØÊåÅÂíåÂª∫ËÆÆ„ÄÇ`);
    }
    
    // Ê∏ÖÁêÜËøáÊúüÊï∞ÊçÆÔºàÂêéÂè∞ÊâßË°åÔºâ
