  },
  
  onShow() {
    // ÈÄöÁü•ÂØºËà™Ê†èÊõ¥Êñ∞Áä∂ÊÄÅ
    tabBarManager.setCurrentIndexByPath('/pages/intervene/chat');
  },
  
  onUnload() {
    // È°µÈù¢Âç∏ËΩΩÊó∂‰øùÂ≠òÊ∂àÊÅØ
    this.saveAllMessages();
  },
  
  methods: {
    /**
     * Âä†ËΩΩ‰ºöËØùÂàóË°®
     */
    async loadSessions() {
      try {
        const sessionsData = uni.getStorageSync('chat_sessions');
        if (sessionsData) {
          this.sessions = JSON.parse(sessionsData);
        } else {
          // ÂàõÂª∫ÈªòËÆ§‰ºöËØù
          this.sessions = [{
            id: 'default',
            name: 'ÈªòËÆ§‰ºöËØù',
            createdAt: Date.now(),
            lastMessageAt: Date.now(),
            messageCount: 0
          }];
          this.saveSessions();
        }
        
        // Êõ¥Êñ∞ÂΩìÂâç‰ºöËØùÂêçÁß∞
        const currentSession = this.sessions.find(s => s.id === this.sessionId);
        if (currentSession) {
          this.currentSessionName = currentSession.name;
        }
        
        console.log(`[CHAT] Âä†ËΩΩ‰∫Ü ${this.sessions.length} ‰∏™‰ºöËØù`);
      } catch (error) {
        console.error('[CHAT] Âä†ËΩΩ‰ºöËØùÂàóË°®Â§±Ë¥•:', error);
      }
    },
    
    /**
     * ‰øùÂ≠ò‰ºöËØùÂàóË°®
     */
    saveSessions() {
      try {
        uni.setStorageSync('chat_sessions', JSON.stringify(this.sessions));
        console.log('[CHAT] ‰ºöËØùÂàóË°®Â∑≤‰øùÂ≠ò');
      } catch (error) {
        console.error('[CHAT] ‰øùÂ≠ò‰ºöËØùÂàóË°®Â§±Ë¥•:', error);
      }
    },
    
    /**
     * ÊòæÁ§∫‰ºöËØùÂàóË°®
     */
    showSessionList() {
      this.showSessionPopup = true;
    },
    
    /**
     * ÂàáÊç¢‰ºöËØù
     */
    async switchSession(session) {
      if (session.id === this.sessionId) {
        this.showSessionPopup = false;
        return;
      }
      
      // ‰øùÂ≠òÂΩìÂâç‰ºöËØùÁöÑÊ∂àÊÅØ
      await this.saveAllMessages();
      
      // ÂàáÊç¢‰ºöËØù
      this.sessionId = session.id;
      this.currentSessionName = session.name;
      this.messages = [];
      
      // Âä†ËΩΩÊñ∞‰ºöËØùÁöÑÊ∂àÊÅØ
      await this.loadHistoryMessages();
      
      // Â¶ÇÊûúÊòØÁ©∫‰ºöËØùÔºåÊ∑ªÂä†Ê¨¢ËøéÊ∂àÊÅØ
      if (this.messages.length === 0) {
        this.addAIMessage('ÊÇ®Â•ΩÔºÅÊàëÊòØÊÇ®ÁöÑÂøÉÁêÜÊîØÊåÅAI„ÄÇÊó†ËÆ∫ÊÇ®ÈÅáÂà∞‰ªÄ‰πàÂõ∞Êâ∞ÔºåÈÉΩÂèØ‰ª•ÂíåÊàëÂÄæËØâ„ÄÇÊàë‰ºöËÆ§ÁúüÂÄæÂê¨ÔºåÂπ∂Â∞ΩÊàëÊâÄËÉΩÁªô‰∫àÊîØÊåÅÂíåÂª∫ËÆÆ„ÄÇ');
      }
      
      this.showSessionPopup = false;
      
      uni.showToast({
        title: `Â∑≤ÂàáÊç¢Âà∞Ôºö${session.name}`,
        icon: 'success',
        duration: 1500
      });
      
      console.log('[CHAT] ÂàáÊç¢Âà∞‰ºöËØù:', session.id);
    },
    
    /**
     * ÂàõÂª∫Êñ∞‰ºöËØù
     */
    handleNewSession() {
      uni.showModal({
        title: 'Êñ∞Âª∫‰ºöËØù',
        content: 'ËØ∑ËæìÂÖ•‰ºöËØùÂêçÁß∞',
        placeholderText: '‰æãÂ¶ÇÔºöÂ∑•‰ΩúÂéãÂäõ„ÄÅÂ≠¶‰π†Âõ∞Êâ∞Á≠â...',
        editable: true,
        confirmText: 'ÂàõÂª∫',
        success: (res) => {
          if (res.confirm) {
            const sessionName = res.content?.trim() || `‰ºöËØù${this.sessions.length + 1}`;
            this.createNewSession(sessionName);
          }
        }
      });
    },
    
    /**
     * ÂàõÂª∫Êñ∞‰ºöËØù
     */
    async createNewSession(name) {
      const newSession = {
        id: `session_${Date.now()}`,
        name: name,
        createdAt: Date.now(),
        lastMessageAt: Date.now(),
        messageCount: 0
      };
      
      this.sessions.unshift(newSession);
      this.saveSessions();
      
      // ÂàáÊç¢Âà∞Êñ∞‰ºöËØù
      await this.switchSession(newSession);
      
      console.log('[CHAT] ÂàõÂª∫Êñ∞‰ºöËØù:', newSession);
    },
    
    /**
     * ÈáçÂëΩÂêç‰ºöËØù
     */
    renameSession(session) {
      uni.showModal({
        title: 'ÈáçÂëΩÂêç‰ºöËØù',
        content: 'ËØ∑ËæìÂÖ•Êñ∞ÁöÑ‰ºöËØùÂêçÁß∞',
        placeholderText: session.name,
        editable: true,
        confirmText: 'Á°ÆÂÆö',
        success: (res) => {
          if (res.confirm && res.content) {
            const newName = res.content.trim();
            if (newName) {
              session.name = newName;
              if (session.id === this.sessionId) {
                this.currentSessionName = newName;
              }
              this.saveSessions();
              
              uni.showToast({
                title: 'ÈáçÂëΩÂêçÊàêÂäü',
                icon: 'success'
              });
              
              console.log('[CHAT] ‰ºöËØùÂ∑≤ÈáçÂëΩÂêç:', session.id, newName);
            }
          }
        }
      });
    },
    
    /**
     * Âà†Èô§‰ºöËØù
     */
    deleteSession(session) {
      uni.showModal({
        title: 'Âà†Èô§‰ºöËØù',
        content: `Á°ÆÂÆöË¶ÅÂà†Èô§‰ºöËØù"${session.name}"ÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§ç„ÄÇ`,
        confirmText: 'Á°ÆÂÆöÂà†Èô§',
        confirmColor: '#DC3545',
        success: async (res) => {
          if (res.confirm) {
            // ‰ªéÂàóË°®‰∏≠ÁßªÈô§
            const index = this.sessions.findIndex(s => s.id === session.id);
            if (index > -1) {
              this.sessions.splice(index, 1);
              this.saveSessions();
            }
            
            // Âà†Èô§‰ºöËØùÁöÑÊâÄÊúâÊ∂àÊÅØ
            await chatStorage.clearSession(session.id);
            
            // Â¶ÇÊûúÂà†Èô§ÁöÑÊòØÂΩìÂâç‰ºöËØùÔºåÂàáÊç¢Âà∞ÈªòËÆ§‰ºöËØù
            if (session.id === this.sessionId) {
              const defaultSession = this.sessions.find(s => s.id === 'default') || this.sessions[0];
              if (defaultSession) {
                await this.switchSession(defaultSession);
              }
            }
            
            uni.showToast({
              title: '‰ºöËØùÂ∑≤Âà†Èô§',
              icon: 'success'
            });
            
            console.log('[CHAT] ‰ºöËØùÂ∑≤Âà†Èô§:', session.id);
          }
        }
      });
