    // ÂèëÈÄÅÊ∂àÊÅØ
    async sendMessage() {
      const text = this.inputText.trim();
      if (!text || this.isSending) return;
      
      // ÊïèÊÑüËØçÊ£ÄÊµã
      const sensitiveCheck = checkSensitiveWords(text);
      
      // Â¶ÇÊûúÂåÖÂê´Âç±Êú∫ÂÖ≥ÈîÆËØçÔºåÊòæÁ§∫Âç±Êú∫Âπ≤È¢ÑÊèêÁ§∫
      if (sensitiveCheck.isCrisis) {
        uni.showModal({
          title: '‚ö†Ô∏è ÈáçË¶ÅÊèêÁ§∫',
          content: getCrisisWarning(),
          confirmText: 'ÊàëÁü•ÈÅì‰∫Ü',
          confirmColor: '#DC3545',
          showCancel: false,
          success: (res) => {
            // Áî®Êà∑Á°ÆËÆ§Âêé‰ªçÁÑ∂ÂèëÈÄÅÊ∂àÊÅØÔºå‰ΩÜ‰ºöÁî±ÂêéÁ´ØËøõË°åÁâπÊÆäÂ§ÑÁêÜ
            this.proceedSendMessage(text, sensitiveCheck);
          }
        });
        return;
      }
      
      // Â¶ÇÊûúÂåÖÂê´ÊïèÊÑüËØç‰ΩÜ‰∏çÊòØÂç±Êú∫ÔºåÊòæÁ§∫Ë≠¶Âëä
      if (sensitiveCheck.hasSensitive) {
        uni.showModal({
          title: 'ÊïèÊÑüÂÜÖÂÆπÊèêÁ§∫',
          content: getSensitiveWarning(sensitiveCheck.matchedWords),
          confirmText: 'ÁªßÁª≠ÂèëÈÄÅ',
          cancelText: 'ÈáçÊñ∞ÁºñËæë',
          success: (res) => {
            if (res.confirm) {
              this.proceedSendMessage(text, sensitiveCheck);
            }
          }
        });
        return;
      }
      
      // Ê≠£Â∏∏ÂèëÈÄÅ
      this.proceedSendMessage(text, null);
    },
    
    /**
     * ÊâßË°åÂèëÈÄÅÊ∂àÊÅØÔºàÂÜÖÈÉ®ÊñπÊ≥ïÔºâ
     */
    async proceedSendMessage(text, sensitiveCheck) {
      // ÂàõÂª∫Áî®Êà∑Ê∂àÊÅØ
      const userMessage = {
        role: 'user',
        content: text,
        timestamp: Date.now(),
        sendStatus: 'sending',  // Ê∑ªÂä†ÂèëÈÄÅÁä∂ÊÄÅ
        hasSensitive: sensitiveCheck?.hasSensitive || false,
        isCrisis: sensitiveCheck?.isCrisis || false
      };
      
      // Ê∑ªÂä†Âà∞Ê∂àÊÅØÂàóË°®
      this.messages.push(userMessage);
      const messageIndex = this.messages.length - 1;
      this.inputText = '';
      this.scrollToBottom();
      
      // ‰øùÂ≠òÁî®Êà∑Ê∂àÊÅØ
      await this.saveMessage(userMessage);
      
      // ÂèëÈÄÅÂà∞AIÂπ∂Ëé∑ÂèñÂõûÂ§ç
      await this.sendToAI(messageIndex);
    },
    
    /**
     * ÊòæÁ§∫‰∫∫Ê†ºÈÄâÊã©Âô®
     */
    showPersonalitySelector() {
      this.showPersonalityPopup = true;
