    /**
     * ÈÄâÊã©AI‰∫∫Ê†º
     */
    selectPersonality(personalityId) {
      if (personalityId === this.currentPersonality) {
        this.showPersonalityPopup = false;
        return;
      }
      
      const oldPersonality = getPersonalityConfig(this.currentPersonality);
      const newPersonality = getPersonalityConfig(personalityId);
      
      // Êõ¥Êñ∞‰∫∫Ê†º
      this.currentPersonality = personalityId;
      
      // ‰øùÂ≠òÂÅèÂ•Ω
      savePersonalityPreference(personalityId);
      
      // ÂÖ≥Èó≠ÂºπÁ™ó
      this.showPersonalityPopup = false;
      
      // ÊèêÁ§∫Áî®Êà∑
      uni.showToast({
        title: `Â∑≤ÂàáÊç¢Ëá≥${newPersonality.name}`,
        icon: 'success',
        duration: 1500
      });
      
      // Ê∑ªÂä†Á≥ªÁªüÊèêÁ§∫
      const systemMessage = {
        role: 'system',
        content: `ÔºàÊÇ®Â∑≤ÂàáÊç¢AI‰∫∫Ê†ºÔºö${oldPersonality.name} ‚Üí ${newPersonality.name}Ôºâ`,
        timestamp: Date.now(),
        isSystem: true
      };
      this.messages.push(systemMessage);
      this.scrollToBottom();
      
      console.log('[CHAT] ÂàáÊç¢AI‰∫∫Ê†º:', oldPersonality.name, '‚Üí', newPersonality.name);
    },
    
    /**
     * ÂèëÈÄÅÊ∂àÊÅØÂà∞AIÂπ∂Â§ÑÁêÜÂõûÂ§ç
     */
    async sendToAI(messageIndex) {
      // ÊòæÁ§∫ËæìÂÖ•‰∏≠Áä∂ÊÄÅ
      this.isSending = true;
      this.isTyping = true;
      
      try {
        // ÂáÜÂ§áÂèëÈÄÅÁöÑÊ∂àÊÅØÂàóË°®ÔºàÂè™ÂåÖÂê´Â∑≤ÊàêÂäüÁöÑÊ∂àÊÅØÔºâ
        const messagesToSend = this.messages
          .filter(msg => msg.sendStatus !== 'failed' && msg.sendStatus !== 'sending' && !msg.isSystem)
          .concat([this.messages[messageIndex]]);
        
        // Ë∞ÉÁî®‰∫ëÂáΩÊï∞Ëé∑ÂèñAIÂõûÂ§ç
        const res = await uniCloud.callFunction({
          name: 'stress-chat',
          data: {
            messages: messagesToSend.map(m => ({
              role: m.role,
              content: m.content
            })),
            personality: this.currentPersonality, // ‰º†ÂÖ•ÂΩìÂâç‰∫∫Ê†º
            stream: false
          }
        });
        
        // Ê∑ªÂä†AIÂõûÂ§çÊ∂àÊÅØ
        if (res.result && res.result.success && res.result.data) {
          const aiContent = res.result.data.content || res.result.data.message;
          
          // Ê†áËÆ∞Áî®Êà∑Ê∂àÊÅØÂèëÈÄÅÊàêÂäü
          this.$set(this.messages[messageIndex], 'sendStatus', 'success');
          await this.saveMessage(this.messages[messageIndex]);
          
          // Ê∑ªÂä†AIÂõûÂ§ç
          this.addAIMessage(aiContent);
        } else {
          console.error('[CHAT] AIÂõûÂ§çÂºÇÂ∏∏:', res);
          // Ê†áËÆ∞Ê∂àÊÅØÂèëÈÄÅÂ§±Ë¥•
          this.$set(this.messages[messageIndex], 'sendStatus', 'failed');
          await this.saveMessage(this.messages[messageIndex]);
          
          uni.showToast({
            title: 'AIÂõûÂ§çÂºÇÂ∏∏ÔºåÁÇπÂáªÈáçÂèë',
            icon: 'none',
            duration: 2000
          });
        }
        
      } catch (error) {
        console.error('[CHAT] ÂèëÈÄÅÂ§±Ë¥•:', error);
        
        // Ê†áËÆ∞Ê∂àÊÅØÂèëÈÄÅÂ§±Ë¥•
        this.$set(this.messages[messageIndex], 'sendStatus', 'failed');
        await this.saveMessage(this.messages[messageIndex]);
        
        uni.showToast({
          title: 'ÂèëÈÄÅÂ§±Ë¥•ÔºåÁÇπÂáªÈáçÂèë',
          icon: 'none',
          duration: 2000
        });
      } finally {
        this.isSending = false;
        this.isTyping = false;
        this.scrollToBottom();
      }
    },
    
    /**
     * ÈáçÂèëÂ§±Ë¥•ÁöÑÊ∂àÊÅØ
     */
    async resendMessage(messageIndex) {
      const message = this.messages[messageIndex];
      
      if (!message || message.role !== 'user') {
        return;
      }
      
      // Êõ¥Êñ∞Áä∂ÊÄÅ‰∏∫ÂèëÈÄÅ‰∏≠
      this.$set(this.messages[messageIndex], 'sendStatus', 'sending');
      
      // ÈúáÂä®ÂèçÈ¶à
      uni.vibrateShort({
        success: () => {
          console.log('[CHAT] ÈáçÂèëÈúáÂä®ÂèçÈ¶à');
        }
      });
      
      // ÈáçÊñ∞ÂèëÈÄÅ
      await this.sendToAI(messageIndex);
    },
    
