            response = 'Áù°Áú†ÈóÆÈ¢òÁ°ÆÂÆûÂæàÂõ∞Êâ∞‰∫∫„ÄÇÂª∫ËÆÆÊÇ®Áù°ÂâçÈÅøÂÖç‰ΩøÁî®ÊâãÊú∫Ôºå‰øùÊåÅËßÑÂæãÁöÑ‰ΩúÊÅØÊó∂Èó¥„ÄÇÊÇ®‰πüÂèØ‰ª•Â∞ùËØïÊàë‰ª¨ÁöÑÂÜ•ÊÉ≥Èü≥ÁñóÂäüËÉΩÔºåÂ∏ÆÂä©ÊîæÊùæË∫´ÂøÉÔºåÊîπÂñÑÁù°Áú†Ë¥®Èáè„ÄÇ';
          } else if (userMsg.includes('Ë∞¢Ë∞¢') || userMsg.includes('ÊÑüË∞¢')) {
            response = '‰∏çÂÆ¢Ê∞îÔºÅÂæàÈ´òÂÖ¥ËÉΩÂ§üÂ∏ÆÂà∞ÊÇ®„ÄÇÂ¶ÇÊûúËøòÊúâ‰ªª‰ΩïÂõ∞Êâ∞ÔºåÈöèÊó∂ÂèØ‰ª•ÂíåÊàëËÅäËÅä„ÄÇËÆ∞ÂæóÁÖßÈ°æÂ•ΩËá™Â∑±ÔºÅ';
          } else {
            response = 'ÊàëÂê¨Âà∞‰∫ÜÊÇ®ÁöÑÂøÉÂ£∞„ÄÇËôΩÁÑ∂ÊàëÂè™ÊòØ‰∏Ä‰∏™AIÔºå‰ΩÜÊàëÁúüËØöÂú∞Â∏åÊúõËÉΩÁªôÊÇ®‰∏Ä‰∫õÊîØÊåÅ„ÄÇÂ¶ÇÊûúÊÇ®ÊÑøÊÑèÔºåÂèØ‰ª•ËØ¶ÁªÜËØ¥ËØ¥ÊÇ®ÁöÑÊÑüÂèóÔºåÊàë‰ºöËÆ§ÁúüÂÄæÂê¨„ÄÇ';
          }
          
          this.addAIMessage(response);
          resolve();
        }, 1500);
      });
    },
    
    // Ê∑ªÂä†AIÊ∂àÊÅØ
    async addAIMessage(content) {
      const aiMessage = {
        role: 'assistant',
        content: content,
        timestamp: Date.now()
      };
      
      this.messages.push(aiMessage);
      this.scrollToBottom();
      
      // ‰øùÂ≠òAIÊ∂àÊÅØ
      await this.saveMessage(aiMessage);
    },
    
    // ÊªöÂä®Âà∞Â∫ïÈÉ®
    scrollToBottom() {
      this.$nextTick(() => {
        this.scrollIntoView = this.getMsgId(this.messages.length - 1);
      });
    },
    
    // Ëé∑ÂèñÊ∂àÊÅØID
    getMsgId(index) {
      return this.msgIdPrefix + index;
    },
    
    /**
     * Âà§Êñ≠Ê∂àÊÅØÊòØÂê¶ÂèØ‰ª•Êí§ÂõûÔºà2ÂàÜÈíüÂÜÖÔºâ
     */
    canRevoke(msg) {
      if (!msg || msg.role !== 'user' || msg.sendStatus !== 'success') {
        return false;
      }
      
      const now = Date.now();
      const messageTime = msg.timestamp || 0;
      const timeDiff = now - messageTime;
      
      // 2ÂàÜÈíü = 120000ÊØ´Áßí
      return timeDiff < 120000;
    },
    
    /**
     * Êí§ÂõûÊ∂àÊÅØ
     */
    async revokeMessage(index) {
      const message = this.messages[index];
      
      if (!message || !this.canRevoke(message)) {
        uni.showToast({
          title: 'Êó†Ê≥ïÊí§ÂõûÊ≠§Ê∂àÊÅØ',
          icon: 'none'
        });
        return;
      }
      
      uni.showModal({
        title: 'Êí§ÂõûÊ∂àÊÅØ',
        content: 'Á°ÆÂÆöË¶ÅÊí§ÂõûËøôÊù°Ê∂àÊÅØÂêóÔºü',
        confirmText: 'Êí§Âõû',
        cancelText: 'ÂèñÊ∂à',
        success: async (res) => {
          if (res.confirm) {
            // Ê†áËÆ∞Ê∂àÊÅØ‰∏∫Â∑≤Êí§Âõû
            this.$set(this.messages[index], 'isRevoked', true);
            this.$set(this.messages[index], 'revokedAt', Date.now());
            
            // ‰øùÂ≠òÊõ¥Êñ∞ÂêéÁöÑÊ∂àÊÅØ
            await this.saveMessage(this.messages[index]);
            
            // ÈúáÂä®ÂèçÈ¶à
            uni.vibrateShort({
              success: () => {
                console.log('[CHAT] Êí§ÂõûÈúáÂä®ÂèçÈ¶à');
              }
            });
            
            // ÂêåÊó∂Ê£ÄÊü•ÊòØÂê¶ÊúâÂØπÂ∫îÁöÑAIÂõûÂ§çÈúÄË¶ÅÊ†áËÆ∞
            // Êü•Êâæ‰∏ã‰∏ÄÊù°AIÊ∂àÊÅØ
            if (index + 1 < this.messages.length && 
                this.messages[index + 1].role === 'assistant') {
              this.$set(this.messages[index + 1], 'relatedRevoked', true);
              await this.saveMessage(this.messages[index + 1]);
            }
            
            uni.showToast({
              title: 'Â∑≤Êí§Âõû',
              icon: 'success',
              duration: 1500
            });
            
            console.log('[CHAT] Ê∂àÊÅØÂ∑≤Êí§Âõû, index:', index);
          }
        }
      });
    },
    
    /**
     * Â§ÑÁêÜÊ∏ÖÁ©∫ËÅäÂ§©
     */
    handleClearChat() {
      if (this.messages.length === 0) {
        uni.showToast({
          title: 'ÊöÇÊó†ËÅäÂ§©ËÆ∞ÂΩï',
          icon: 'none'
        });
        return;
      }
      
      uni.showModal({
        title: 'Ê∏ÖÁ©∫ËÅäÂ§©ËÆ∞ÂΩï',
        content: 'Á°ÆÂÆöË¶ÅÊ∏ÖÁ©∫ÊâÄÊúâËÅäÂ§©ËÆ∞ÂΩïÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§ç„ÄÇ',
        confirmText: 'Á°ÆÂÆöÊ∏ÖÁ©∫',
        confirmColor: '#DC3545',
        success: (res) => {
          if (res.confirm) {
            this.clearCurrentSession();
          }
        }
      });
    },
    
    /**
     * ÊòæÁ§∫ËØÑÂàÜÂØπËØùÊ°Ü
     */
    showRatingDialog(msg, index) {
      uni.showActionSheet({
        itemList: ['üëç ÂæàÊúâÂ∏ÆÂä©', 'üëé ‰∏çÂ§üÊª°ÊÑè', 'üí° Êèê‰æõÂèçÈ¶à'],
        success: (res) => {
          const tapIndex = res.tapIndex;
          
          if (tapIndex === 0) {
            // Â•ΩËØÑ
            this.rateMessage(msg, index, 'good');
          } else if (tapIndex === 1) {
            // Â∑ÆËØÑ
            this.rateMessage(msg, index, 'bad');
            // ËØ¢ÈóÆÊòØÂê¶Êèê‰æõËØ¶ÁªÜÂèçÈ¶à
            this.askForDetailedFeedback(msg, index);
          } else if (tapIndex === 2) {
            // Áõ¥Êé•Êèê‰æõÂèçÈ¶à
            this.askForDetailedFeedback(msg, index);
          }
        }
      });
    },
    
    /**
     * ËØÑ‰ª∑Ê∂àÊÅØ
     */
    async rateMessage(msg, index, rating) {
      // Êõ¥Êñ∞Êú¨Âú∞Ê∂àÊÅØÁä∂ÊÄÅ
      this.$set(this.messages[index], 'rating', rating);
      this.$set(this.messages[index], 'ratedAt', Date.now());
      
      // ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
      await this.saveMessage(this.messages[index]);
      
      // Êèê‰∫§ËØÑÂàÜÂà∞ÊúçÂä°Âô®ÔºàÂºÇÊ≠•Ôºå‰∏çÂΩ±ÂìçÁî®Êà∑‰ΩìÈ™åÔºâ
      this.submitRating(msg, rating).catch(err => {
        console.warn('[CHAT] ËØÑÂàÜ‰∏ä‰º†Â§±Ë¥•:', err);
      });
      
      // ÈúáÂä®ÂèçÈ¶à
      uni.vibrateShort({
        success: () => {
          console.log('[CHAT] ËØÑÂàÜÈúáÂä®ÂèçÈ¶à');
        }
      });
      
      // ÊòæÁ§∫ÊÑüË∞¢ÊèêÁ§∫
      uni.showToast({
        title: rating === 'good' ? 'ÊÑüË∞¢ÊÇ®ÁöÑÂèçÈ¶àÔºÅ' : 'Êàë‰ª¨‰ºöÂä™ÂäõÊîπËøõ',
        icon: 'success',
        duration: 1500
      });
      
      console.log('[CHAT] Ê∂àÊÅØËØÑÂàÜ:', rating);
    },
    
    /**
     * Êèê‰∫§ËØÑÂàÜÂà∞ÊúçÂä°Âô®
     */
    async submitRating(msg, rating, feedback = '') {
      try {
        const res = await uniCloud.callFunction({
          name: 'chat-feedback',
          data: {
            sessionId: this.sessionId,
            messageContent: msg.content,
            messageTimestamp: msg.timestamp,
            rating: rating,
            feedback: feedback,
            timestamp: Date.now()
          }
        });
        
        if (res.result && res.result.success) {
          console.log('[CHAT] ËØÑÂàÜÂ∑≤Êèê‰∫§');
        }
      } catch (error) {
        console.error('[CHAT] ËØÑÂàÜÊèê‰∫§Â§±Ë¥•:', error);
        // Â§±Ë¥•‰∏çÂΩ±ÂìçÁî®Êà∑‰ΩìÈ™åÔºå‰ªÖËÆ∞ÂΩïÊó•Âøó
      }
    },
    
    /**
     * ËØ¢ÈóÆËØ¶ÁªÜÂèçÈ¶à
     */
    askForDetailedFeedback(msg, index) {
      // Âª∂ËøüÊòæÁ§∫ÔºåÈÅøÂÖç‰∏éËØÑÂàÜÊìç‰ΩúÂÜ≤Á™Å
      setTimeout(() => {
        uni.showModal({
          title: 'Êèê‰æõÂèçÈ¶à',
          content: 'ËØ∑ÂëäËØâÊàë‰ª¨ÊÇ®ÁöÑÊÉ≥Ê≥ïÔºåÂ∏ÆÂä©Êàë‰ª¨ÊîπËøõAIÂõûÂ§çË¥®Èáè',
          placeholderText: '‰æãÂ¶ÇÔºöÂõûÂ§çÂ§™ÁÆÄÁü≠„ÄÅ‰∏çÂ§ü‰∏ì‰∏öÁ≠â...',
          editable: true,
          confirmText: 'Êèê‰∫§ÂèçÈ¶à',
          success: (res) => {
            if (res.confirm && res.content) {
              // Êèê‰∫§ËØ¶ÁªÜÂèçÈ¶à
              this.submitRating(msg, msg.rating || 'neutral', res.content);
              
              uni.showToast({
                title: 'ÊÑüË∞¢ÊÇ®ÁöÑÂèçÈ¶àÔºÅ',
                icon: 'success'
              });
            }
          }
        });
      }, 300);
    }
  }
}
</script>

